.calming-container {
  background: linear-gradient(to bottom right, #ffc0cb, #d9d8fc, #ffffff);
  padding: 2rem;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
  color: #2d2d2d;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.logo {
  width: 60px;
  height: 60px;
  margin-bottom: 0.5rem;
}

.subheading {
  color: #5a5a5a;
  margin-top: 0.2rem;
}

.search-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 1rem auto;
  padding: 0.4rem 1rem;
  max-width: 300px;
  border-radius: 25px;
  background: #fff;
  box-shadow: 0 0 5px rgba(0,0,0,0.1);
}

.search-container input {
  border: none;
  outline: none;
  width: 100%;
}

.category-title {
  margin-top: 2rem;
  font-size: 1.4rem;
  font-weight: bold;
}

.track-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.track-item {
  background: #fff;
  border-radius: 15px;
  padding: 0.8rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
